<template>
    <div v-for="item of list" :key="item.id">
        <Card :content="item" :correspondenceOfFieldsAndRusLabels="correspondenceOfFieldsAndRusLabels"/>
    </div>
</template>


<script>
import Axios from '@/axios'
import Card from '@/components/basic/Card.vue'

export default {
    components:{ Card },
    props:{
        getUrl: String,
        correspondenceOfFieldsAndRusLabels: Object
    },
    data(){
        return{
            list:[]
        }
    },
    async created(){
        this.list = (await Axios.get(this.getUrl)).data
    }
}
</script>